.about__container{
    box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 
                0 8px 16px -8px rgba(0,0,0,.3), 
                0 -6px 16px -6px rgba(0,0,0,.025);
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    margin: 0 auto;
    margin-top: 100px;
    width: 500px;
    max-height: 10%;
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;            
}

.about__container span{
    font-size: 20px;
    display: flex;
    justify-content: center;
}