.movies .movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    /* display: flex;
    align-items: flex-start;
    justify-content: space-between; */
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    /* position: relative; */
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,.25),
                0 8px 16px -8px rgba(0,0,0,.3), 
                0 -6px 16px -6px rgba(0,0,0,.025);
}

.movies .movie a{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    text-decoration: none;
    color: inherit;
}

.movie img{
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 
                0 8px 16px -8px rgba(0,0,0,.3), 
                0 -6px 16px -6px rgba(0,0,0,.025);
    
}

.movie .movie__title,
.mocie .movie__year{
    font-weight: 300;
    margin: 0;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c
}

.movie .movie__genres{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    margin: 5px 0px;
}

.movie__genres li{
    margin-right: 10px;
    font-size: 14px;
}