.nav{
    position: fixed;
    top: 50px;
    left: 10px;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 10px 20px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 
                0 8px 16px -8px rgba(0,0,0,.3), 
                0 -6px 16px -6px rgba(0,0,0,.025);
    border-radius: 5px;
}

.nav a{
    text-decoration: none;
    color: #34495e;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
}

.nav a:first-child{
    margin-bottom: 20px;
}
